"use strict";
import { InterfaceElementName, InterfaceEventName } from "@uniswap/analytics-events";
import MobileAppLogo from "assets/svg/uniswap_app_logo.svg";
import Modal from "components/Modal";
import { useConnectorWithId } from "components/WalletModal/useOrderedConnections";
import { CONNECTION } from "components/Web3Provider/constants";
import { useConnect } from "hooks/useConnect";
import { useCallback, useEffect, useState } from "react";
import { CloseIcon } from "theme/components";
import { Button, Flex, Image, QRCodeDisplay, Separator, Text, useSporeColors } from "ui/src";
import { sendAnalyticsEvent } from "uniswap/src/features/telemetry/send";
import { useTranslation } from "uniswap/src/i18n";
import { isWebAndroid, isWebIOS } from "utilities/src/platform";
import { openDownloadApp } from "utils/openDownloadApp";
export default function UniwalletModal() {
  const { t } = useTranslation();
  const [uri, setUri] = useState();
  const connection = useConnect();
  const onLaunchedMobilePlatform = isWebIOS || isWebAndroid;
  const open = !onLaunchedMobilePlatform && !!uri && connection.isPending;
  const uniswapWalletConnectConnector = useConnectorWithId(CONNECTION.UNISWAP_WALLET_CONNECT_CONNECTOR_ID, {
    shouldThrow: true
  });
  useEffect(() => {
    function listener({ type, data }) {
      if (type === "display_uniswap_uri" && typeof data === "string") {
        setUri(data);
      }
    }
    uniswapWalletConnectConnector.emitter.on("message", listener);
    return () => {
      uniswapWalletConnectConnector.emitter.off("message", listener);
    };
  }, [uniswapWalletConnectConnector.emitter]);
  const close = useCallback(() => {
    connection?.reset();
    setUri(void 0);
  }, [connection]);
  useEffect(() => {
    if (open) {
      sendAnalyticsEvent(InterfaceEventName.UNIWALLET_CONNECT_MODAL_OPENED);
    } else {
      setUri(void 0);
    }
  }, [open]);
  const colors = useSporeColors();
  return <Modal isOpen={open} onDismiss={close}><Flex shrink grow p="$spacing20">
    <Flex row justifyContent="space-between">
      <Text variant="subheading1">{t("account.drawer.modal.scan")}</Text>
      <CloseIcon onClick={close} />
    </Flex>
    <Flex row my="$spacing24" centered>{uri && <QRCodeDisplay
      ecl="M"
      color={colors.accent1.val}
      containerBackgroundColor={colors.surface1.val}
      encodedValue={uri}
      size={370}
      eyeSize={140}
    ><Flex borderRadius="$rounded32" borderWidth="$spacing8" borderColor="$surface2"><Image src={MobileAppLogo} width={81} height={81} /></Flex></QRCodeDisplay>}</Flex>
    <Separator />
    <Flex centered row pt="$spacing20" justifyContent="space-between" gap="$spacing20">
      <Flex shrink>
        <Text variant="subheading2">{t("account.drawer.modal.dont")}</Text>
        <Text variant="body3" color="$neutral2">{t("account.drawer.modal.body")}</Text>
      </Flex>
      <Button
        size="small"
        onPress={() => openDownloadApp({ element: InterfaceElementName.UNISWAP_WALLET_MODAL_DOWNLOAD_BUTTON })}
        height="fit-content"
      >{t("common.download")}</Button>
    </Flex>
  </Flex></Modal>;
}
